import React, { useEffect, useRef } from "react";
import { graphql, Link } from "gatsby";
import styled from "styled-components";
import Layout from "../components/layout";
import { respondTo } from "../components/shared/_respondTo";
import { Helmet } from "react-helmet";
import { markdownCSS } from "../components/shared/markdown";
import hljs from "../components/shared/highlightConfig";

const BlogContainer = styled.div`
  max-width: 100%;
`;

const BlogPost = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  ${respondTo.md`
    width:100%;
  `}
`;

const BlogHeader = styled.div`
  width: 100%;
  border-bottom: 1px solid #cfcfcf;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
`;

const BlogTitle = styled.h1`
  font-size: 36px;
  margin-bottom: 16px;
  ${respondTo.md`
    font-size: 24px;
  `}
`;

const BlogDate = styled.h5`
  font-size: 12px;
`;

const BlogPostContent = styled.div`
  font-size: 15px;
  padding-top: 24px;
  white-space: pre-wrap;
  background-color: var(--background);
  color: var(--fontColor);

  ${respondTo.md`
    width: 100%;
    padding-left: 16px;
    padding-right: 16px;
  `}

  ${markdownCSS}
`;

const BlogPagination = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 3rem;
`;

const BlogComment = styled.div`
  width: 100%;
  border-top: 1px solid #cfcfcf;
  margin-top: 24px;
  padding: 0 16px;

  .utterances {
    margin: 0;
    max-width: 100%;
  }
`;

export default function Template({
  data,
  pageContext, // this prop will be injected by the GraphQL query below.
}: {
  data: any;
  pageContext: any;
}) {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark;
  const { next, previous } = pageContext;
  const commentDivRef = useRef<HTMLDivElement>(null);
  const contentDivRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const script = document.createElement("script");
    script.src = "https://utteranc.es/client.js";
    script.setAttribute("issue-term", "pathname");
    script.setAttribute("repo", "juyoungpark718/juyoungpark718.github.io");
    script.setAttribute("theme", "github-light");
    script.crossOrigin = "anonymous";
    script.async = true;
    commentDivRef.current?.appendChild(script);
  }, []);

  useEffect(() => {
    contentDivRef.current?.querySelectorAll("pre code").forEach(el => {
      hljs.highlightElement(el as HTMLElement);
    });
  });

  const checkMobile = () => {
    return window.outerWidth <= 480 ? true : false;
  };

  // const matchSquareBracket = () => {
  //   const regExp = /\[(.*?)\]/;
  //   if (checkMobile()) {
  //     const word = frontmatter.title.match(regExp);

  //     if (word) {
  //       const others = frontmatter.title.replace(word[0], "");
  //       return { word: word[0], others };
  //     } else return { word: null, others: frontmatter.title };
  //   } else {
  //     return frontmatter.title;
  //   }
  // };

  return (
    <Layout>
      {/* @ts-ignore */}
      <Helmet title={frontmatter.title} />
      <BlogContainer>
        <BlogPost>
          <BlogHeader>
            <BlogTitle>
              {frontmatter.title}
              {/* {matchSquareBracket() ? (
                matchSquareBracket().word ? (
                  <>
                    <div>{matchSquareBracket().word}</div>
                    <div>{matchSquareBracket().others}</div>
                  </>
                ) : (
                  matchSquareBracket().others
                )
              ) : (
                matchSquareBracket()
              )} */}
            </BlogTitle>
            <BlogDate>{frontmatter.date}</BlogDate>
          </BlogHeader>
          <BlogPostContent
            ref={contentDivRef}
            dangerouslySetInnerHTML={{ __html: html }}
          />
          <BlogComment ref={commentDivRef}></BlogComment>
          {/* <BlogPagination>
            {previous ? (
              <Link to={previous.frontmatter.path}>
                {`<- ${previous.frontmatter.title}`}
              </Link>
            ) : (
              <span></span>
            )}
            {next ? (
              <Link
                to={next.frontmatter.path}
              >{`${next.frontmatter.title} ->`}</Link>
            ) : (
              <span></span>
            )}
          </BlogPagination> */}
        </BlogPost>
      </BlogContainer>
    </Layout>
  );
}

export const pageQuery = graphql`
  query ($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "YYYY MM DD, HH:mm")
      }
    }
  }
`;
